.page {
  font-family: "Poppins", sans-serif;
  background-color: #1d1d1d;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);

    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 90px;

    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 160px;
    }
  }

  &__section-title {
    margin: 0 0 50px;
    font-weight: 500;
    font-size: 32px;
    line-height: 32px;

    text-align: center;
    letter-spacing: -1.5px;
    text-transform: capitalize;

    color: #fff;

    @include onTablet {
      font-size: 48px;
      line-height: 100%;
    }

    @include onDesktop {
      font-size: 64px;
      line-height: 100%;
    }
  }
}
